import Vue from 'vue'
import Router from 'vue-router'

import Home from '@/pages/Home'
import Portfolio from '@/pages/Portfolio'
import Contact from '@/pages/Contact'
import Cv from '@/pages/Cv'

Vue.use(Router)

const router = new Router({
  routes: [
    { path: '/', name: 'Home', component: Home },
    { path: '/portfolio', name: 'Portfolio', component: Portfolio, meta: { title: 'ToruPrints - 3D printing Portfolio' } },
    { path: '/contact', name: 'Contact', component: Contact, meta: { title: 'ToruPrints - Contact Form' } },
    { path: '/cv', name: 'Cv', component: Cv, meta: { title: 'ToruPrints - Cirriculum Vitea' } }
  ]
})

const DEFAULT_TITLE = 'ToruPrints - 3D Printing commissions and CI/CD Consultancy'
router.beforeEach((to, from, next) => {
  document.title = to.meta.title || DEFAULT_TITLE

  next() // make sure to always call next()!
})

export default router



