<template>
    <div class="contact-form router-content">
        <h2>Contact/Commision Enquiry</h2>
        <ContactForm v-show="!complete" @submitted="submitted"></ContactForm>
        <ThankYou v-show="complete"></ThankYou>
    </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'contact',
  data: function () {
    return {
      complete: false
    }
  },
  methods: {
    submitted: function (response) {
      var data = this.$store.state.contactForm

      axios.post('https://6j10wa597e.execute-api.eu-west-1.amazonaws.com/default/ToruPrintsEmail', data)
        .then(response => {
          // JSON responses are automatically parsed.
          console.log(response.data)
        })
        .catch(e => {
          console.log(e)
        })

      this.complete = true
    }
  }
}
</script>

<style lang="scss">
    @import '../styles/theme.scss';
</style>



