<template>
    <div class="router-content">
        <div class="home-logo">
        </div>
        <h2>ToruPrints</h2>
        <p>
            This site is to allow me to show off what I can do, and to allow people to contact me.
        </p>
        <p>
            If you're here for 3D printing commisions then check out my portfolio for a list of things I've done and the different levels of service I can provide
        </p>
        <p>
            If you're here because you're curious about what I can provide in terms of CI/CD consultation, then check out my C.V. and browse around the website to see what I'm capable of
        </p>
    </div>
</template>

<script>
export default {
  name: 'home'
}
</script>

<style lang="scss">
    @import '../styles/theme.scss';

    .home-logo {
      content: url('../assets/logo.svg');
      height: 200;
      width: 200;
      margin-left: auto;
      margin-right: auto;
    }
</style>



