<template>
  <div id="app">
    <div class="menu-holder">
      <div class="menu" @click="openMenu = !openMenu" @mouseleave="openMenu = false" :class="{ opened: openMenu }">
        <div class="title">
          <div class="logo">
          </div>
          <label>Menu</label>
        </div>
        <router-link class="menu-link" to="/" v-show="openMenu">Home</router-link>
        <router-link class="menu-link" to="/portfolio" v-show="openMenu">Portfolio</router-link>
        <router-link class="menu-link" to="/contact" v-show="openMenu">Contact</router-link>
        <router-link class="menu-link" to="/cv" v-show="openMenu">C.V.</router-link>
        <a href="https://twitter.com/timrunfast" target="_blank" title="Go to my fanastical twitter feed" @click.stop="openMenu = false"><div class="logo-twitter" /></a>
      </div>
    </div>
    <router-view />
  </div>
</template>

<script>
export default {
  name: 'App',
  data: function () {
    return {
      openMenu: false
    }
  }
}
</script>

<style lang="scss">
    @import './styles/theme.scss';

    #app {
      text-align: center;
      color: $secondaryColour;
      min-height: 100vh;
      background-color: white;
      width: 75vw;
    }

    @media screen and (max-width: $mediaMaxWidth) {
      #app {
        width: 100vw;
      }
    }

    .router-content {
      z-index: 10;
      margin-top: 55px;
    }

    h1, h2, h3, h4 {
        font-family: $fontFamilyHeader;
        font-weight: bold;
    }

    .menu-holder {
      text-align: left;
      display: flex;
      flex-direction: column;
      position: absolute;
      width: 150px;
      z-index: 1;

      .menu {
        display: flex;
        flex-direction: column;
        width: 150px;
        padding: 15px 5px;

        &.opened {
          border: 1px solid $secondaryColour;
          border-left: 0;
          border-top: 0;
          z-index: 100;
          background-color: white;
        }

        .menu-link {
          margin-left: 15px;
          padding: 5px;

          &:hover {
            background-color: $primaryColour;
          }
        }
      }

      .logo {
        content: url('./assets/logo.svg');
        height: $lineHeight * 2;
        width: $lineHeight * 2;
      }

      .logo-twitter {
        content: url('./assets/twitter-brands.svg');
        height: $lineHeight;
        width: $lineHeight;
        padding: 5px 10px;
      }

      .title {
        display: flex;
        flex-direction: row;
        line-height: $lineHeight;
        vertical-align: middle;
        cursor: pointer;

        label {
          margin-top: auto;
          margin-bottom: auto;
          cursor: pointer;
        }
      }
    }
</style>



