// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import Vuex from 'vuex'
import App from './App'
import router from './router'
import './styles/main.scss'
import newsItems from './data/NewsItems.json'

Vue.use(Vuex)

Vue.config.productionTip = false

Vue.component('NewsItem', () => import('./components/NewsItem.vue'))
Vue.component('SelectedItem', () => import('./components/SelectedItem.vue'))
Vue.component('NewsFeed', () => import('./components/NewsFeed.vue'))
Vue.component('MaterialInput', () => import('./components/MaterialInput.vue'))
Vue.component('Recaptcha', () => import('./components/Recaptcha.vue'))
Vue.component('ContactForm', () => import('./components/ContactForm.vue'))
Vue.component('ThankYou', () => import('./components/ThankYou.vue'))

const store = new Vuex.Store({
  state: {
    selectedItem: null,
    newsItems: newsItems,
    contactForm: {
      email: '',
      name: '',
      message: ''
    }
  },
  mutations: {
    setSelected (state, payload) {
      state.selectedItem = payload.item
    },
    clearSelected (state) {
      state.selectedItem = null
    },
    nextSelected (state) {
      var currentIndex = newsItems.findIndex(function (el) {
        return el.id === state.selectedItem.id
      })

      state.selectedItem = newsItems[currentIndex !== newsItems.length - 1 ? ++currentIndex : 0]
    },
    prevSelected (state) {
      var currentIndex = newsItems.findIndex(function (el) {
        return el.id === state.selectedItem.id
      })

      state.selectedItem = newsItems[currentIndex !== 0 ? --currentIndex : newsItems.length - 1]
    },
    setContactEmail (state, payload) {
      state.contactForm.email = payload.item
    },
    setContactName (state, payload) {
      state.contactForm.name = payload.item
    },
    setContactMessage (state, payload) {
      state.contactForm.message = payload.item
    }
  }
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  store,
  router,
  components: { App },
  template: '<App/>'
})



