<template>
    <div class="router-content">
        <h2>Cirriculum Vitea</h2>
        <div class="cv-holder">
            <div class="link-holder">
                <a @click="tab = 'Intro'" :class="{ selected: tab === 'Intro'}">Intro</a>
                <a @click="tab = 'Experience'" :class="{ selected: tab === 'Experience'}">Experience</a>
                <a @click="tab = 'Skills'" :class="{ selected: tab === 'Skills'}">Skills</a>
                <a :href="require('../assets/Curriculum Vitae.pdf')" target="_blank">Download</a>
            </div>
            <div class="current-page">
                <div v-show="tab === 'Intro'">
                    <h3>Introduction</h3>
                    <p>
                        I'm a developer who has been in the industry since 2008. I've gone up the ranks from Graduate developer to Senior and Tech Lead.
                        I've taken the role of Admin of Dev Ops in 4 companies and introduced a pipeline to allow for increased development via automated deployments and testing.
                        In some of these companies deployments were taking a developer half a day to complete and now they take no developer time and notify the correct team when features or bug fixes hit their applicable environment.
                        There has been integration between the Deployment tool and ticket management systems that allow for easier tracking of ticket state; which environment it's currently in, and who is currently working on it.
                    </p>
                </div>
                <div v-show="tab === 'Experience'">
                    <h3>Experience</h3>
                    <div v-for="(job, jobIndex) in experience" :key="jobIndex">
                        <h4 class="company">{{ job.company }}</h4>
                        <div class="date-range">
                            <small>{{ job.startDate | formatDate }} - {{ job.endDate | formatDate }}</small>
                        </div>
                        <div v-for="(position, posIndex) in job.positions" :key="posIndex" class="position">
                            <label class="title">{{ position.title }}</label>
                            <p>{{ position.description }}</p>
                        </div>
                        <hr />
                    </div>
                </div>
                <div v-show="tab === 'Skills'">
                    <h3>Skills</h3>
                    <p>I've grouped my skills depending on how much time I've had with them: Some indicates I've dabbled either in my spare time or briefly in the industry, Lots means I've worked with it pretty much constantly. If the skill is in bold then it means I've used it in a job</p>
                    <div v-for="(skillGroup, skillGroupIndex) in skillsGroup" :key="skillGroupIndex">
                        <h4>{{ skillGroup.key }}</h4>
                        <div class="skills">
                            <label v-for="(skill, skillIndex) in skillGroup.skills" :key="skillIndex" class="skill" :class="{ industry: skill.industry}">{{ skill.name }}</label>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import experience from '../data/Experience.json'
import skills from '../data/Skills.json'
import moment from 'moment'

export default {
  name: 'cv',
  data: function () {
    return {
      tab: 'Intro',
      experience: experience,
      skills: skills
    }
  },
  computed: {
    skillsGroup: function () {
      const map = new Map()
      var skillsGroup = []
      var keyGetter = skill => skill.level

      skills.forEach((item) => {
        const key = keyGetter(item)
        const collection = map.get(key)
        if (!collection) {
          map.set(key, [item])
        } else {
          collection.push(item)
        }
      })

      Array.from(map).forEach(function (el) {
        var obj = {
          key: el[0],
          skills: el[1]
        }

        skillsGroup.push(obj)
      })

      return skillsGroup
    }
  },
  filters: {
    formatDate: function (value) {
      if (!value) return 'present'

      return moment(String(value), 'DD/MM/YYYY').format('MMMM YYYY')
    }
  }
}
</script>

<style lang="scss">
    @import '../styles/theme.scss';

    .cv-holder {
        display: flex;
        flex-direction: row;
        justify-content: space-evenly;
        height: 90vh;

        .link-holder {
            display: flex;
            flex-direction: column;
            width: 10vw;
            text-align: left;

            a {
                border: 1px solid $secondaryColour;
                margin-bottom: 15px;
                padding: 10px 5px;
                cursor: pointer;
                text-decoration: none;
                color: $secondaryColour;

                &:hover {
                    -webkit-box-shadow: 2px 2px 5px 0px rgba(0,0,0,0.75);
                    -moz-box-shadow: 2px 2px 5px 0px rgba(0,0,0,0.75);
                    box-shadow: 2px 2px 5px 0px rgba(0,0,0,0.75);
                }

                &.selected {
                    background-color: $primaryColour;
                    font-weight: bold;
                }
            }
        }

        .current-page {
            border: 1px solid $secondaryColour;
            width: 50vw;
            overflow-y: auto;
            padding: 5px 15px;
            text-align: left;

            .company {
                text-align: center;
            }

            .date-range {
                text-align: center;
                position: relative;
                top: -$lineHeight;
                color: lightgray;
                font-size: 0.8em;
                font-weight: italic;
            }

            .position {
                .title {
                    font-weight: bold;
                }
            }

            .skills {
                display: flex;
                flex-direction: row;
                flex-basis: 15%;
                flex-wrap: wrap;

                .skill {
                    margin-left: 10px;
                    margin-right: 10px;

                    &.industry {
                        font-weight: bold;
                    }
                }
            }
        }
    }

    @media screen and (max-width: $mediaMaxWidth) {
        .cv-holder {
            flex-direction: column;
            justify-content: flex-start;

            .link-holder {
                flex-direction: row;
                width: 100vw;
                justify-content: space-evenly;

                a {
                    min-width: 20%;
                }
            }

            .current-page {
                width: 90vw;
                margin-left: auto;
                margin-right: auto;
            }
        }
    }
</style>



