<template>
    <div class="router-content">
        <h2>Portfolio</h2>
        <div>
            <NewsFeed v-show="selectedItem === null"></NewsFeed>
            <SelectedItem v-show="selectedItem !== null"></SelectedItem>
        </div>
    </div>
</template>

<script>
export default {
  name: 'portfolio',
  computed: {
    selectedItem: function () {
      return this.$store.state.selectedItem
    }
  }
}
</script>

<style lang="scss">
    @import '../styles/theme.scss';
</style>



